html {
  background-color: #eeeeee;
}
body {
  margin: 0;
}
a,
button,
input {
  -webkit-tap-highlight-color: rgba(255, 0, 0, 0);
}
a {
  text-decoration: none;
  color: #000;
}
a:hover {
  background-color: none;
}
a:hover,
a:active {
  color: #c40000;
  background-color: #FFF;
  text-decoration: none;
}
::selection {
  background-color: #FFF;
  /* 此属性为 选中设置 */
  text-shadow: none;
}
.horizontal-view {
  display: -webkit-box;
  display: -moz-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
  -moz-box-orient: horizontal;
  -moz-box-direction: normal;
  -webkit-flex-direction: row;
  -ms-flex-direction: row;
  flex-direction: row;
}
.img {
  background-color: transparent;
  background-size: contain;
  background-repeat: no-repeat;
}
.swiper-container {
  width: 10rem;
  height: 5.9rem;
}
.swiper-container img {
  width: 100%;
  height: 100%;
}
.swiper-slide {
  background: #fff;
}
.comWidth {
  width: 10rem;
  margin: 0 auto;
}
.vertical-view {
  width: 10rem;
  height: 4.594rem;
  background-color: #ffffff;
}
.vertical-fis {
  margin-left: 0.359rem;
  width: 9.641rem;
  height: 2.125rem;
}
.vertical-li {
  text-align: center;
  margin-left: 0.047rem;
  width: 2.265rem;
  height: 2.251rem;
  position: relative;
}
.vertical-li .icon-shumajiadian {
  background-color: #4caf50;
  line-height: 1.20rem;
}
.vertical-li .icon-meizhuang {
  background-color: #ff7549;
  line-height: 1.35rem;
}
.vertical-li .icon-dtshangyi {
  background-color: #2cb8f8;
}
.vertical-li .icon-gouwuche {
  background-color: #d4237a;
}
.vertical-li .icon-geren {
  background-color: #13227a;
  line-height: 1.20rem;
}
.vertical-li .icon-shoucang {
  background-color: #0d5ee4;
  line-height: 1.20rem;
}
.vertical-li .icon-liwu1 {
  background-color: #d81e06;
}
body,
button,
input,
select,
textarea {
  font: 12px/1.5 Arial, Verdana, "\5b8b\4f53";
  -ms-overflow-style: scrollbar;
}
.spacing {
  box-sizing: border-box;
  line-height: 0;
  width: 10rem;
  height: 0.156rem;
  background-color: #eeeeee;
  position: relative;
  background-position: initial initial;
  background-repeat: initial initial;
}
.vertical-top {
  width: 1.25rem;
  height: 1.25rem;
  border-radius: 100%;
  background-color: #FFC107;
  line-height: 1.25rem;
  font-size: 0.75rem;
  color: #fff;
  margin: 0.313rem auto 0;
}
.vertical-text {
  line-height: 0.688rem;
